.Projects{
padding: 20vh 0px;
margin: auto;
/* border: 1px solid red; */
height: auto;
width: 76vw;
}

.project-div-1{
    /* border: 1px solid teal; */
    padding: 20px 0px;
}
.project-div-img-laptop ,.project-div-img-mobile{
    /* border: 1px solid rgb(2, 128, 0); */
    /* width: 32vw; */
    justify-content: center;
    align-items: center;
}
.project-div-wrapper{
    /* border: 1px solid rgb(11, 0, 128); */
    display: flex;
    /* width: 76vw; */
}
.project-dis-div-2{
/* border: 1px solid rgb(11, 0, 128); */
    margin-top: 20px;
    padding: 25px 0px;
}
.project-img-laptop {
    /* border:  1px solid red; */
    height: 300px;
    /* width: 80vw; */
    object-fit: contain;
    mix-blend-mode: multiply;
 
}

.project-img-mobile{
    height: 266px;
    width: 36.9vw;
    object-fit: contain;
    mix-blend-mode: multiply;
}
.project-div-img-laptop,.project-div-img-mobile{
     border-bottom: 2px solid black;

    display: flex;
    height: 300px;
    width: 76vw;
    gap: 10px;
}

.github-demo-button{
margin-top: 30px;
}
button{
    background-color: transparent;
    width: 200px;
    height: 60px;
    cursor: pointer;
;
}
button:hover{
    background-color: rgba(0, 0, 0, 0.077);

    cursor: pointer;
;
}


.github{
    mix-blend-mode: multiply;
    height: 25px;
    width: 25px;
}

.Demo:hover{
    cursor: pointer;
}
 

