@media screen and (max-width: 1080px) {
    .project-div-wrapper {
        /* border: 1px solid red; */
        display: flex;
        flex-direction: column;
     
        width: 76vw;
        gap: 10px;
        height: auto;
    }

    .project-hr{
        width: 56vw;
    }
    .project-div-img-laptop {
        /* border: 1px solid red; */
        width: 76vw;
        height: 200px;
        object-fit: contain;
    }
    button{
        width: 150px;
        height: 40px;
    }

    .Projects {
        padding: 15vh 0px;
       
    }
}
